<script>
import moment from 'moment'
import { Printd } from 'printd'
import Service from './service'
export default {
  name: 'ResultDialog',
  components: {},
  props: {
    printingId: {},
    showType: {
      type: String
    }
  },
  data() {
    return {
      dialogVisible: false,
      infoList: {},
      itemList: [],
      houseItemList: [],
      pieItemList: [],
      dy:"",
      start:"",
      end:"",
      tableData: [],
      pieData: [],
      pieTableData:[]
    };
  },
  methods: {

    handleClose(done) {
      this.dialogVisible = false
    },
    show() {
      this.dialogVisible = true
    },
    onPrintFun() {
        const cssText = `

         .el-table th.is-leaf, .el-table td {
              border-bottom: 1px solid #ebeef5;
          }
          .el-table--border th, .el-table--border td {
              border-right: 1px solid #ebeef5;
          }
        `
      const d = new Printd()
      d.print(document.getElementById('printBox'), [cssText  ])
    },
    totalFormatter(row, column){
        return row.todayNumber+row.todayNumber2+row.todayNumber3+row.todayNumber4;
    },
    getServer (params) {
        this.dy=params.dy
        if(params.dy=='总数'){
            this.start=moment(params.start).format("YYYY-MM-DD")
            this.end=moment(params.end).format("YYYY-MM-DD")
            Service.getItemNewCard(params).then(res => {
                this.itemList = res.data
            })

            Service.getItemHouse(params).then(res => {
                this.houseItemList = res.data
            })

            Service.getItemPie(params).then(res => {
                this.pieItemList = res.data
            })
        }else if(params.dy=='小区房屋统计'){

            Service.getHouseTable({}).then(res => {
                this.tableData = res.data
            })
        }else if(params.dy=='小区人口统计'){
            Service.getPieTableData({}).then(res => {
                this.pieTableData = res.data
            })
        }

    },
    splitMessage (str) {
      let result = '<span style="line-height:20px">' + str.split('；').join('</span><br><span style="line-height:20px">') + '</span>'
      return result
    },
    contactMessage(visitPersons) {
      return visitPersons.join(',')
    },
    extractMessage(checkPersons) {
      let extractStr = ''
      checkPersons.forEach(item => {
        extractStr += item.name + '，'
        extractStr += '联系方式：' + item.contactInfo + '；'
      })
      return this.splitMessage(extractStr)
    }
  },
  watch: {
    printingId (val) {
      this.getServer(val)
    }
  },
  computed: {
    dialogTitle () {
      return this.showType === 'print' ? '打印' : '打印'
    }
  }
}
</script>

