        <div class="card" :class="`item${index+1}`" @click="gotoFeedback(item.label,1)" style="cursor: pointer;">
          <div class="title">{{item.label}}</div>
          <div class="num">{{item.number}}</div>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="6" v-for="(item, index) in pieItemList" :key="index" >
        <div class="card" :class="`item${index+1}`" @click="gotoFeedback(item.label,2)" style="cursor: pointer;">
