      </el-col>
      <el-col :span="4">
        <div style="text-align: right">
          <el-button size="small" type="primary" @click="downT1">导出</el-button>
          <el-button size="small" type="primary" @click="showPrintingDialog2()">打印</el-button>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20" class="middle">
      <el-col :span="8">
        <div class="piechart">
          <div class="tName">房屋构成</div>
          <ve-donut-chart :tooltip="tooltip" :data="chartData"/>
        </div>
      </el-col>
      <el-col :span="16">
        <div class="tName">房屋列表</div>
        <div class="table">
          <el-table
            :data="tableData"
            height="400"
            size="small"
            style="width: 100%">
            <el-table-column
              prop="communityName"
              label="小区名称">
            </el-table-column>
            <el-table-column
              prop="officer"
              label="负责民警">
            </el-table-column>
            <el-table-column label="房屋套数">
              <el-table-column
                prop="total"
                label="房屋总数">
              </el-table-column>
              <el-table-column
                prop="self"
                label="自住套数">
              </el-table-column>
              <el-table-column
                prop="rent"
                label="出租套数">
              </el-table-column>
              <el-table-column
                prop="empty"
                label="空置套数">
              </el-table-column>
              <el-table-column
