          <el-button size="small" type="primary" @click="downT2">导出</el-button>
          <el-button size="small" type="primary" @click="showPrintingDialog3()">打印</el-button>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20" class="middle">
      <el-col :span="8">
        <div class="piechart">
          <div class="tName">人员构成</div>
          <ve-donut-chart :tooltip="tooltip" :data="pieChartData"/>
        </div>
      </el-col>
