        <div class="table">
          <el-table
            :data="pieTableData"
            height="400"
            size="small"
            style="width: 100%">
            <el-table-column
              prop="communityName"
              label="小区名称">
            </el-table-column>
            <el-table-column
              prop="officer"
              label="负责民警">
            </el-table-column>
            <el-table-column label="人口统计">
              <el-table-column
                prop="todayNumber"
                :formatter="totalFormatter"
                label="人口总数">
              </el-table-column>
              <el-table-column
                prop="todayNumber"
                label="常住人口">
              </el-table-column>
              <el-table-column
                prop="todayNumber2"
                label="暂住人口">
              </el-table-column>
              <el-table-column
                prop="todayNumber3"
                label="寄住人口">
              </el-table-column>
              <el-table-column
