      <el-col :span="16">
        <div class="linechart">
          <div class="tName">异常数据趋势图</div>
          <ve-line-chart :grid="grid" :data="lineData" :settings="chartSettings"/>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="tName">小区异常排行榜</div>
        <div class="table">
          <el-table
            height="400"
            :header-cell-style="bgObj"
            border
            :data="areaData"
            size="small"
            style="width: 100%">
            <el-table-column
              align="center"
              prop="communityName"
              label="小区名称">
            </el-table-column>
            <el-table-column
              prop="number"
              align="center"
              label="异常数据">
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
    <printing-dialog :showType="showType" :printingId="printingId" ref="printingDialog"></printing-dialog>
  </div>

